import { Component, OnInit } from '@angular/core';
import { FileUploader } from 'ng2-file-upload';
import { PropuestasService } from '../../services/propuestas.service';
import { Propuesta } from '../../model/propuesta';
import { Router } from '@angular/router';

const uploadAPI = 'http://clh-tramos-business-api-dev-tramos-piloto.apps.ocp-esc.losheroes.int/clh-poc-business-api/files'; // better use a service class

@Component({
  selector: 'app-resumen-proceso',
  templateUrl: './resumen-proceso.component.html',
  styleUrls: ['./resumen-proceso.component.css']
})

export class ResumenProcesoComponent implements OnInit {

  
  public uploader: FileUploader = new FileUploader({ url: uploadAPI, itemAlias: 'file' });

  constructor(private propuestasService:PropuestasService, private router: Router) { }
  showUploader:boolean = false;
  porcentaje: number;

  user:string;
  pass:string;
  rutPersona:string;
  empresa:string;
  rutEmpresa:string;
  avance:number;
  trabajadoresActualizados:number;
  trabajadoresPendientes:number;
  trabajadoresTotal:number;
  nombreTrabajador:string;
  rutTrabajador:string;
  propuestas:Propuesta[];
  ultimaPropuesta:Propuesta;



  ngOnInit() {
    this.user = localStorage.getItem("user");
    this.pass = localStorage.getItem("password");
    this.rutPersona = localStorage.getItem("rutPersona");
    this.empresa = localStorage.getItem("empresa");
    this.rutEmpresa = localStorage.getItem("rutEmpresa");
    this.porcentaje = 0;
    this.trabajadoresActualizados = 0;
    this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };
    this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
      console.log('FileUpload:uploaded successfully:', item, status, response);
      alert('Declaracin cargada correctamente');
      this.showUploader = false;
      this.propuestasService.completeTask(this.rutEmpresa, this.user, this.pass).subscribe((result)=>{
        console.info(result);
      });
    }


    this.propuestasService.getCurrentTask(this.rutEmpresa, this.user, this.pass).subscribe((result)=>{
      console.info(result);
      this.propuestas  =  result;
      this.trabajadoresTotal = this.propuestas.length;
      this.rutEmpresa = this.propuestas[0].rutEmpresa;
      for (let propuesta of this.propuestas) {
        if(propuesta.est2=="Declarado")
          this.trabajadoresActualizados++;
      }
      this.trabajadoresPendientes = this.trabajadoresTotal-this.trabajadoresActualizados;
      this.porcentaje = this.trabajadoresActualizados*this.trabajadoresPendientes/this.trabajadoresTotal;
      if(this.propuestas[0].est2=="Declarado")
      this.ultimaPropuesta = this.propuestas[0];

     })
  }

  mostrarUploader(){
    console.info("Se muestra uploader");
    this.showUploader=true;
  }

  descargarCSV(){
    this.propuestasService.downloadCSV(this.rutEmpresa, this.user, this.pass).subscribe((result)=>{
      const blob = new Blob([result], { type: 'application/octet-stream' });
      const url= window.URL.createObjectURL(blob);
      var anchor = document.createElement("a");
      anchor.download = this.rutEmpresa + ".csv";
      anchor.href = url;
      anchor.click();      
      console.info("descargando archivos");
    });
  }
  salir(){
  this.router.navigate(['/login']);
  }
  

  mostrarHistorial(){
    this.router.navigate(['/historial-actualizaciones']);
  }


}
