import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ResumenProcesoComponent } from './components/resumen-proceso/resumen-proceso.component';
import { HistorialActualizacionesComponent } from './components/historial-actualizaciones/historial-actualizaciones.component';
import { LoginComponent } from './components/login/login.component';


const routes: Routes = [
  { path: '', redirectTo: 'resumen-proceso', pathMatch: 'full'},
  { path: 'resumen-proceso', component: ResumenProcesoComponent },
  { path: 'historial-actualizaciones', component: HistorialActualizacionesComponent },
  { path: 'login', component: LoginComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
