import { Injectable } from '@angular/core';
import { Propuesta } from '../model/propuesta';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PropuestasService {
  
  urlBase:String = "http://clh-tramos-business-api-dev-tramos-piloto.apps.ocp-esc.losheroes.int/clh-poc-business-api";

  constructor(private http:HttpClient) { }

  public getCurrentTask(rutEmpresa:String, username:String, pass:String): Observable<Propuesta[]> {
    return this.http.get<Propuesta[]>(this.urlBase+"/propuesta/bpm?usuario="+  username + "&pass=" + pass);
  }
  public downloadCSV(rutEmpresa:String, username:String, pass:String): Observable<any>{		
		return this.http.get(this.urlBase+"/files/csv?rutEmpresa=" + rutEmpresa + "&user=" + username + "&pass=" + pass, { responseType: 'blob'});
   }

   public completeTask(rutEmpresa:String, username:String, pass:String): Observable<any> {
    return this.http.post<any>(this.urlBase+"/propuesta/bpm/completeTask?user="+  username + "&pass=" + pass + "&rutEmpresa=" + rutEmpresa, null);
  }
}
