import { Component, OnInit } from '@angular/core';
import { Propuesta } from 'src/app/model/propuesta';
import { PropuestasService } from 'src/app/services/propuestas.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-historial-actualizaciones',
  templateUrl: './historial-actualizaciones.component.html',
  styleUrls: ['./historial-actualizaciones.component.css']
})
export class HistorialActualizacionesComponent implements OnInit {


  user:string;
  pass:string;
  rutPersona:string;
  empresa:string;
  rutEmpresa:string;
  avance:number;
  trabajadoresActualizados:number;
  trabajadoresPendientes:number;
  trabajadoresTotal:number;
  nombreTrabajador:string;
  rutTrabajador:string;
  propuestas:Propuesta[];
  propuestas2:Propuesta[];

  
  constructor(private propuestasService:PropuestasService, private router: Router) { }

  ngOnInit() {
    this.user = localStorage.getItem("user");
    this.pass = localStorage.getItem("password");
    this.rutPersona = localStorage.getItem("rutPersona");
    this.empresa = localStorage.getItem("empresa");
    this.rutEmpresa = localStorage.getItem("rutEmpresa");
    this.propuestas2 = new Array();
    this.propuestasService.getCurrentTask(this.rutEmpresa, this.user, this.pass).subscribe((result)=>{
      console.info(result);
      this.propuestas  =  result;
      this.rutEmpresa = this.propuestas[0].rutEmpresa;
      for (let propuesta of this.propuestas) {
        if(propuesta.est2=="Declarado")
          this.propuestas2.push(propuesta);
      }
      
     })
  }

  salir(){
    this.router.navigate(['/login']);
    }
  
    mostrarResumen(){
      this.router.navigate(['/resumen-proceso']);
    }

}
